import React from 'react'

export default function MusicWorldAbout() {
    return (
        <div style={{ lineHeight: '1.8rem' }}>
            <div className="App-link">
                About Music World
          <hr className="newhr"></hr>
            </div>
            <div className='col'>
                <div>For generations music has been uninspiring us with </div>
                <div>Generic Beats, Generic Sounds, and Stagnant genres. </div>
                <br></br>
                <div>Music World aims to change that by changing the way
              we create music.</div>
                <div>Music World brings together artists from all over the world to
    collaborate on their music. </div>
                <div>Create new sounds, genes, and make music inspirational again.</div>
            </div>
        </div>
    )
}
