import React from "react";
import Sunset from "../pics/sunset/d.jpg";
import Carousel2 from "../carousel/Carousel2";
import HomePageHeading from './HomePageHeading'

export default function HomeImg() {
  return (
    <div className="" style={{ top: '0' }}>

      <form className='search-form absoluteTop'
        style={{
          top: '8%',
          zIndex: "1"
        }}>

        <input type='text' className='search-bar  ' value=''
          style={{
            borderRadius: '999px',

          }}>
        </input>

        <button type='submit' className='search-button'
          style={{
            borderRadius: '990px',
            marginLeft: '1%', width: '6%'
          }}>Search </button>

      </form >

      <div className="relative">
        <img
          src={Sunset}
          alt=""
          className=" imgLanding fluid  "
          style={{
            justifyContent: "center",
            zIndex: "-3",

            marginTop: "0%",
          }}
        />
        <div className="overlay-image "></div>
        <div className='car2 absoluteTopMidHome'
          style={{
            marginLeft: "-2%", width: '100vw'
          }}
        >
          <Carousel2 />  </div>


        <div className='car2 absoluteTopMidHome'
          style={{
            zIndex: "5", marginLeft: "", width: ''
          }}
        >
          <HomePageHeading />  </div>
        {/* <div
          className=""
          style={{
            zIndex: "1",
            position: "absolute ",
          }}
        >
           <div
            style={{
              width: "100vw",
              fontFamily: "Arial",
              textAlign: "center",
              fontSize: '2.2rem',
            }}
            className="absoluteTopMidHome"
          >
            Home Page
                   
          </div> 


        </div> */}
      </div>
    </div>
  );
}
